@import '../../styles/main.scss';

.box {
  margin: auto;
  height: 100vh;

  overflow: hidden;
  z-index: 0;

  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: center;

  text-align: center;

  background-size: contain;
  background-repeat: no-repeat;
  background-position: right;

  background-image: url(../../img/edward-howell-unsplash-mob.jpg);

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(../../img/edward-howell-unsplash-mob@2x.jpg);
  }

  @media screen and (min-width: 768px) {
    background-image: url(../../img/edward-howell-unsplash-tablet.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../../img/edward-howell-unsplash-tablet@2x.jpg);
    }
  }

  @media screen and (min-width: 1440px) {
    background-image: url(../../img/edward-howell-unsplash-dt.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../../img/edward-howell-unsplash-dt@2x.jpg);
    }
  }
}

.example {
  width: 520px;
  height: 550px;
  flex-shrink: 0;
  border-radius: 550px;
  background: rgba(64, 112, 205, 0.5);
  filter: blur(104.8543701171875px);

  position: absolute;
  top: 20px;
  left: -399px;
  z-index: -999;

  @media screen and (min-width: 768px) {
    border-radius: 829.931px;
    width: 784px;
    height: 830px;
    top: -50px;
    left: -665px;
  }

  @media screen and (min-width: 1440px) {
    border-radius: 849px;
    height: 849px;
    top: -231;
    left: -494px;
  }
}

