  flex-shrink: 0;
  border-radius: 257px;
  background: rgba(188, 230, 210, 0.4);
  filter: blur(104.8543701171875px);

  position: absolute;
  top: 84px;
  left: 102px;
  z-index: -999;

  @media screen and (min-width: 768px) {
    border-radius: 387px;
    width: 387px;
    height: 372px;
    top: 46px;
    left: 0px;
  }

