    left: 171px;
  }
}

.example3 {
  width: 549px;
  height: 543px;
  flex-shrink: 0;
  border-radius: 549px;
  background: rgba(64, 112, 205, 0.5);
  filter: blur(104.8543701171875px);

  position: absolute;
  bottom: -229px;
  right: -505px;
  z-index: -999;

  @media screen and (min-width: 768px) {
    bottom: -17;
    right: -402px;
  }

